import {
	basePath
} from '@/utils/index'
import api from '@/api/order';
import memberapi from '@/api/member';
import categoryapi from '@/api/category';
import {
	mapState
} from 'vuex'
import tuiButton from "@/components/extend/button/button"
import tuiListCell from "@/components/list-cell/list-cell"
import tuiBottomPopup from "@/components/bottom-popup/bottom-popup"
import tuiNumberbox from "@/components/numberbox/numberbox"
export default {
	components: {
		tuiButton,
		tuiListCell,
		tuiBottomPopup,
		tuiNumberbox
	},
	data() {
		return {
			basePath,
			hasCoupon: true,
			insufficient: false,
			orderList: [],
			orderInfo: {
				name: "",
				price: null,
				img_url: []
			}, //订单信息
			value: 1, //订单数量
			productAmount: 0.00, //商品总金额
			amount: 0.00, //支付金额
			postage: 0.00, //邮费
			userInfo: {
				province: "", //省
				city: "", //市
				district: "", //区
				address: "", //详细地址
				contact_name: "", //收货人姓名
				contact_phone: "", //收货人手机号
			},
			hasAddress: false,
			seladdressId: null, //选择的地址id
			productId: null, //商品id
			//规格
			specInfo:{
				id:null,
				name:'',
				price:0,
			}


		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'member_id', 'openid']),
	onLoad: function(options) {

		this.seladdressId = options.addressId ? options.addressId : null;
		this.value = options.qyt ? options.qyt : 1;
		
		if (options.ids) {
			let arr = JSON.parse(options.ids)
			console.log(JSON.stringify(arr))
			let shopCartArr = uni.getStorageSync('shopCartList');
			if (shopCartArr) {
				let orderList = JSON.parse(shopCartArr);
				orderList.forEach(item => {
					arr.forEach(items => {							
						if (items.id == item.id) {
							item.qyt = items.qyt;
							this.orderList.push(item)
						}
					})
				})

				console.log("购物车数组" + JSON.stringify(this.orderList))
			}

		} else {
			this.productId = options.id;
			this.specInfo.id=options.specId;
			this.getProductShow();
		}
		setTimeout(() => {
			this.getAddress();
			//this.calcOrder();
		}, 50)


	},

	onShow: function(e) {


		let pages = getCurrentPages();


		let currPage = pages[pages.length - 1];


		if (currPage.data.isDoRefresh == true) {
			this.seladdressId = currPage.data.addressId ? currPage.data.addressId : null;

			currPage.data.isDoRefresh = false;


			this.getAddress();


		} else {


			//不用刷新


		}


	},
	methods: {
		//获取全部收货地址
		getAddress() {
			var param = {
				page: this.$pagination.page,
				limit: this.$pagination.limit
			}
			var url = memberapi.getAddress + '/' + this.member_id;
			this.$postajax(url, param)
				.then(res => {
					//console.log(JSON.stringify(res))
					if (res.code == 0) {
						let addressList = [];
						if (res.data && res.data.length > 0) {
							if (this.seladdressId) {
								addressList = res.data.filter(item => {
									if (item.id == this.seladdressId) {
										return item
									}
								});
							} else {
								addressList = res.data.filter(item => {
									if (item.is_default == 1) {
										return item
									}
								});

							}
							console.log("是神惡魔" + JSON.stringify(addressList))
							this.userInfo = addressList[0];
							this.hasAddress = addressList[0] ? true : false;
						}
						this.calcOrder();
					}
				})
				.catch(err => {

				});
		},
		//获取产品详情
		getProductShow() {
			var param = {
				member_id: this.member_id,
			}
			var url = categoryapi.getProductShow + '/' + this.productId
			this.$postajax(url, param)
				.then(res => {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						this.orderInfo = res.data;
						this.orderInfo.img_url = JSON.parse(res.data.img_url);
						this.orderInfo.qyt = this.value;
						this.orderInfo.id=this.specInfo.id;
						
						if(this.orderInfo.spec&&this.orderInfo.spec.length>0){
							this.orderInfo.spec.forEach(item=>{
								if(item.id==this.specInfo.id){
									this.specInfo=item;
									
								}
								
							})
						}
						this.orderInfo.price=this.specInfo.price;
						this.orderList.push(this.orderInfo)
						
						
						//console.log("图片数组"+JSON.stringify(this.orderInfo.img_url))
						//this.productAmount=parseFloat(this.orderInfo.price)*this.value;
						/* this.calcOrder(); */
					}


				})
				.catch(err => {

				});
		},
		//计算订单
		calcOrder() {
			let productAmount = 0;
			var goodsList = this.orderList.map(item => {
				var items = {
					"id": item.id,
					"qty": item.qyt,
				}
				productAmount += item.price * item.qyt;
				return items
			})
			//var goodsList=[{"id":this.orderInfo.id,"qty":this.value}];
			var param = {
				id:this.productId,
				member_id: this.member_id,
				goodsList: JSON.stringify(goodsList),
				province: this.userInfo.province,
				city: this.userInfo.city,
				district: this.userInfo.district,
				city: this.userInfo.city,
				address: this.userInfo.address,
				contact_name: this.userInfo.contact_name,
				contact_phone: this.userInfo.contact_phone
			}
			console.log("商品"+JSON.stringify(goodsList))
			this.$postajax(api.calcOrder, param)
				.then(res => {
					console.log("计算商品" + JSON.stringify(res))
					if (res.code == 0) {
						this.amount = res.data.amount; //支付金额
						this.postage = this.amount - productAmount;
					}


				})
				.catch(err => {

				});
		}, //
		/* chooseAddr(){
			uni.chooseAddress({
			  success(res) {
				  this.userInfo=res;
			    console.log(res.userName)
			    console.log(res.postalCode)
			    console.log(res.provinceName) 
			    console.log(res.cityName)
			    console.log(res.countyName)
			    console.log(res.detailInfo)
			    console.log(res.nationalCode)
			    console.log(res.telNumber)
			  }
			})
		}, */
		//计算商品金额
		change: function(e) {
			console.log(JSON.stringify(e.index))
			//this.value = e.value;	
			this.orderList[e.index].qyt = e.value;
			//this.productAmount=parseFloat(this.orderInfo.price)*this.value;
			this.calcOrder();
		},
		//打开选择收货地址
		chooseAddr() {
			let ischooseAddr = true
			uni.navigateTo({
				url: "../address/address?ischooseAddr=" + ischooseAddr
			})
		},
		//提交订单
		submitOrder() {

			// uni.getProvider({

			//     service: 'payment',

			//     success: function (res) {

			//     //在这里进行业务逻辑处理

			//     }

			// });
			let productAmount = 0;
			var goodsList = this.orderList.map(item => {
				var items = {
					"id": item.id,
					"qty": item.qyt,
				}
				productAmount += item.price * item.qyt;
				return items
			})
			//var goodsList=[{"id":this.orderInfo.id,"qty":this.value}];
			var param = {
				id:this.productId,
				member_id: this.member_id,
				goodsList: JSON.stringify(goodsList),
				province: this.userInfo.province,
				city: this.userInfo.city,
				district: this.userInfo.district,
				city: this.userInfo.city,
				address: this.userInfo.address,
				contact_name: this.userInfo.contact_name,
				contact_phone: this.userInfo.contact_phone
			}
			if (!this.userInfo.contact_phone) {
				this.tui.toast("请选择收货地址", 2000, true);
				return
			}
			this.$postajax(api.submitOrder, param)
				.then(res => {
					console.log("计算商品" + JSON.stringify(res))
					if (res.code == 0) {
						let orderInfo = res.data;
						//微信支付

						uni.requestPayment({
							provider: 'wxpay',
							orderInfo: orderInfo, //订单数据
							timeStamp: orderInfo.timeStamp,
							nonceStr: orderInfo.nonceStr,
							package: orderInfo.package,
							signType: orderInfo.signType,
							paySign: orderInfo.paySign,
							service: 3,
							success: function(res) {

								//处理业务逻辑
								this.tui.toast("提交订单成功", 2000, true);
								uni.navigateTo({
									url: "../success/success?orderStatus=2"
								})

							},

							fail: function(err) {

								console.log('fail:' + JSON.stringify(err));
								uni.showToast({

									icon: 'none',

									title: '支付失败'

								});
								setTimeout(() => {
									uni.navigateTo({
										url: "../myOrder/myOrder"
									})
								}, 200)
							}

						});
					}
				})
				.catch(err => {

				});
		},
	}
}
